/* src/ResultsPage.module.css */
.pageWrapper {
    font-family: 'Arial', sans-serif;
    background: linear-gradient(135deg, #4a3359 0%, #6b4968 50%, #8b5a7e 100%);
    min-height: 100vh;
    padding: 40px 20px;
    position: relative;
    overflow-x: hidden;
    color: white;
}

/* Background Emojis */
.emojiBg {
    position: absolute;
    font-size: 4rem;
    opacity: 0.1;
    pointer-events: none;
    z-index: 0;
}
.e1 { top: 5%; left: 5%; }
.e2 { bottom: 10%; right: 5%; font-size: 6rem; }

.container {
    max-width: 1200px;
    margin: 0 auto;
    position: relative;
    z-index: 1;
    text-align: center;
}

.title {
    font-size: 3rem;
    color: #ffd166;
    text-transform: uppercase;
    letter-spacing: 4px;
    margin-bottom: 10px;
    text-shadow: 3px 3px 0px #ff6b6b;
}

.subtitle {
    font-size: 1.2rem;
    color: rgba(255,255,255,0.8);
    margin-bottom: 50px;
}

.grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    gap: 30px;
    justify-content: center;
    padding: 20px 0;
}

.backButton {
    background: rgba(255, 255, 255, 0.1);
    color: white;
    border: 1px solid rgba(255, 255, 255, 0.3);
    padding: 10px 25px;
    border-radius: 30px;
    cursor: pointer;
    margin-bottom: 30px;
    transition: all 0.3s;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
}

.backButton:hover {
    background: rgba(255, 255, 255, 0.2);
    transform: translateX(-5px);
}