/* App.css */

/* Main Wrapper to ensure styles don't leak */
.cc-wrapper {
  font-family: 'Arial', sans-serif;
  background: linear-gradient(135deg, #4a3b5c 0%, #6b4c6f 50%, #7a5980 100%);
  min-height: 100vh;
  padding: 20px;
  position: relative;
  overflow-x: hidden;
  color: white;
}

/* Decorative emojis */
.cc-emoji-decoration {
  position: fixed;
  font-size: 60px;
  opacity: 0.1;
  z-index: 0;
  pointer-events: none;
  user-select: none;
}

.cc-emoji-1 { top: 10%; left: 5%; }
.cc-emoji-2 { top: 60%; left: 8%; }
.cc-emoji-3 { top: 20%; right: 10%; }
.cc-emoji-4 { bottom: 15%; right: 5%; }
.cc-emoji-5 { top: 70%; left: 85%; }

.cc-container {
  max-width: 900px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.cc-header {
  text-align: center;
  margin-bottom: 40px;
}

.cc-logo {
  font-size: 48px;
  font-weight: bold;
  color: #ffb84d;
  text-shadow: 3px 3px 0px #ff6b6b;
  letter-spacing: 8px;
  margin-bottom: 10px;
}

.cc-subtitle {
  color: #ff9999;
  font-size: 24px;
  letter-spacing: 4px;
  margin-bottom: 15px;
}

.cc-tagline {
  color: #ffd699;
  font-style: italic;
  font-size: 18px;
  margin-bottom: 20px;
}

.cc-badge {
  display: inline-block;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 25px;
  padding: 10px 30px;
  color: #fff;
  font-size: 14px;
  letter-spacing: 2px;
}

/* Stats Section */
.cc-stats-container {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 30px;
  margin-bottom: 30px;
  border: 2px solid rgba(255, 255, 255, 0.2);
}

.cc-stats-title {
  color: #ffb84d;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
}

.cc-candidate-card {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 15px;
  padding: 20px;
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 2px solid rgba(255, 255, 255, 0.1);
  transition: transform 0.3s ease, border-color 0.3s ease;
}

.cc-candidate-card:hover {
  transform: translateX(5px);
  border-color: rgba(255, 184, 77, 0.5);
}

.cc-candidate-info {
  flex: 1;
}

.cc-candidate-name {
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 5px;
}

.cc-candidate-roll {
  color: #ff9999;
  font-size: 14px;
}

.cc-vote-count {
  background: linear-gradient(135deg, #ff6b6b, #ffb84d);
  color: white;
  padding: 15px 30px;
  border-radius: 50px;
  font-size: 24px;
  font-weight: bold;
  min-width: 80px;
  text-align: center;
}

/* Voting Section */
.cc-voting-section {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 30px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  text-align: center;
}

.cc-voting-title {
  color: #ffb84d;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.cc-input-group {
  margin-bottom: 20px;
}

.cc-roll-input {
  width: 100%;
  max-width: 400px;
  padding: 15px 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  font-size: 16px;
  text-align: center;
  outline: none;
  transition: border-color 0.3s ease;
}

.cc-roll-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.cc-roll-input:focus {
  border-color: #ffb84d;
}

.cc-vote-btn {
  background: linear-gradient(135deg, #ff6b6b, #ff9966);
  color: white;
  border: none;
  padding: 15px 60px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 50px;
  cursor: pointer;
  letter-spacing: 2px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 5px 15px rgba(255, 107, 107, 0.4);
}

.cc-vote-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(255, 107, 107, 0.6);
}

.cc-vote-btn:active {
  transform: translateY(0);
}

/* Messages */
.cc-error-message {
  color: #ff6b6b;
  text-align: center;
  margin-top: 15px;
  font-size: 14px;
}

.cc-success-message {
  color: #66bb6a;
  text-align: center;
  margin-top: 15px;
  font-size: 14px;
}

/* Modal */
.cc-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cc-modal-content {
  background: linear-gradient(135deg, #5a4a6c 0%, #7a5a8c 100%);
  border-radius: 25px;
  padding: 40px;
  max-width: 500px;
  width: 90%;
  border: 3px solid rgba(255, 184, 77, 0.5);
  animation: cc-slideIn 0.3s ease;
}

@keyframes cc-slideIn {
  from { transform: translateY(-50px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.cc-modal-title {
  color: #ffb84d;
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
}

.cc-modal-candidate {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 20px;
  margin-bottom: 20px;
  text-align: center;
}

.cc-modal-candidate-name {
  color: white;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.cc-modal-candidate-roll {
  color: #ff9999;
  font-size: 16px;
}

.cc-modal-message {
  color: #ffd699;
  text-align: center;
  margin-bottom: 25px;
  font-size: 16px;
}

.cc-modal-buttons {
  display: flex;
  gap: 15px;
  justify-content: center;
}

.cc-modal-btn {
  padding: 12px 30px;
  border: none;
  border-radius: 50px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  letter-spacing: 1px;
  transition: transform 0.3s ease;
}

.cc-modal-btn:hover {
  transform: translateY(-2px);
}

.cc-confirm-btn {
  background: linear-gradient(135deg, #4caf50, #66bb6a);
  color: white;
}

.cc-revote-btn {
  background: linear-gradient(135deg, #ff9800, #ffb74d);
  color: white;
}

.cc-cancel-btn {
  background: rgba(255, 255, 255, 0.2);
  color: white;
}

/* Add these new styles to the end of your App.css */

/* Modal List Container */
.cc-modal-list {
  max-height: 300px;
  overflow-y: auto;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Individual Selection Item */
.cc-modal-list-item {
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  padding: 15px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cc-modal-list-item:hover {
  background: rgba(255, 184, 77, 0.2);
  border-color: #ffb84d;
  transform: translateX(5px);
}

.cc-modal-item-name {
  color: white;
  font-weight: bold;
  font-size: 18px;
}

.cc-modal-item-roll {
  color: #ff9999;
  font-size: 14px;
}

/* Custom Scrollbar for the list */
.cc-modal-list::-webkit-scrollbar {
  width: 8px;
}

.cc-modal-list::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.cc-modal-list::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 4px;
}

.cc-modal-list::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 184, 77, 0.5);
}
/* App.css - Add to bottom */

/* The card for the candidate the user previously voted for */
.cc-modal-list-item.cc-selected {
  background: linear-gradient(135deg, rgba(76, 175, 80, 0.3), rgba(102, 187, 106, 0.3));
  border: 2px solid #66bb6a;
  box-shadow: 0 0 15px rgba(102, 187, 106, 0.2);
}

/* The "YOUR VOTE" Badge */
.cc-current-vote-badge {
  background: #66bb6a;
  color: white;
  font-size: 10px;
  padding: 4px 8px;
  border-radius: 12px;
  margin-left: 10px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: inline-block;
  vertical-align: middle;
}