/* src/ComedyClub.module.css */

/* Wrapper to simulate the body background and layout */
.pageWrapper {
    font-family: 'Arial', sans-serif;
    background: linear-gradient(135deg, #4a3359 0%, #6b4968 50%, #8b5a7e 100%);
    min-height: 100vh;
    padding: 20px;
    position: relative;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* --- DECORATIVE BACKGROUND EMOJIS --- */
.emojiBg {
    position: absolute;
    font-size: 4rem;
    opacity: 0.15;
    pointer-events: none;
    z-index: 0;
    animation: float 6s ease-in-out infinite;
}

.e1 { top: 5%; left: 5%; animation-delay: 0s; }
.e2 { top: 15%; right: 10%; animation-delay: 1s; }
.e3 { bottom: 10%; left: 10%; animation-delay: 2s; }
.e4 { bottom: 20%; right: 5%; animation-delay: 3s; }

@keyframes float {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-20px); }
}

/* --- TOTAL VOTES BADGE --- */
.totalVotesBadge {
    position: absolute;
    top: 20px;
    right: 20px;
    background: rgba(255, 107, 107, 0.95);
    color: white;
    padding: 12px 30px;
    border-radius: 50px;
    font-size: 1.1rem;
    font-weight: bold;
    box-shadow: 0 4px 15px rgba(0,0,0,0.3);
    z-index: 100;
    border: 2px solid rgba(255,255,255,0.2);
    transition: transform 0.2s ease;
}

.totalVotesBadge:hover {
    transform: scale(1.05);
}

/* --- MAIN CONTAINER --- */
.mainContainer {
    max-width: 800px;
    width: 100%;
    text-align: center;
    position: relative;
    z-index: 1;
    margin-top: 60px;
}

/* --- HEADER --- */
.header {
    margin-bottom: 50px;
}

.title {
    font-size: 3.5rem;
    font-weight: 800;
    color: #ffd166;
    text-shadow: 4px 4px 0px #ff6b6b, 8px 8px 0px rgba(0,0,0,0.2);
    letter-spacing: 6px;
    margin: 0 0 10px 0;
}

.subtitle {
    color: #ff6b6b;
    font-size: 1.5rem;
    letter-spacing: 4px;
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 15px;
}

.tagline {
    color: #ffd166;
    font-style: italic;
    font-size: 1.1rem;
    opacity: 0.9;
}

/* --- VOTE BUTTON --- */
.voteBtnContainer {
    margin: 40px 0;
}

.mainVoteBtn {
    background: linear-gradient(135deg, #ff6b6b, #ff8787);
    color: white;
    border: none;
    padding: 22px 60px;
    font-size: 1.4rem;
    font-weight: bold;
    border-radius: 50px;
    cursor: pointer;
    box-shadow: 0 6px 20px rgba(255, 107, 107, 0.4);
    transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    text-transform: uppercase;
    letter-spacing: 2px;
    position: relative;
    overflow: hidden;
}

.mainVoteBtn:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 30px rgba(255, 107, 107, 0.6);
}

.mainVoteBtn:active {
    transform: translateY(-2px);
}

/* --- INFO CARD --- */
.infoCard {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    padding: 30px;
    border-radius: 20px;
    border: 1px solid rgba(255,255,255,0.1);
    color: white;
    max-width: 600px;
    margin: 40px auto;
}

.infoCard h3 {
    color: #ffd166;
    margin-bottom: 10px;
    font-size: 1.4rem;
}

/* --- MODAL STYLES --- */
.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.85);
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease, visibility 0.3s;
}

.modalOverlay.active {
    opacity: 1;
    visibility: visible;
}

.modalContent {
    background: linear-gradient(135deg, #5a4269 0%, #7b5978 100%);
    width: 90%;
    max-width: 450px;
    border-radius: 25px;
    padding: 30px;
    position: relative;
    box-shadow: 0 20px 50px rgba(0,0,0,0.5);
    border: 1px solid rgba(255,255,255,0.1);
    transform: scale(0.8);
    transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.modalOverlay.active .modalContent {
    transform: scale(1);
}

.closeBtn {
    position: absolute;
    top: 15px;
    right: 15px;
    background: rgba(0,0,0,0.2);
    border: none;
    color: white;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    font-size: 1.5rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background 0.2s;
}

.closeBtn:hover {
    background: rgba(255, 107, 107, 0.8);
}

.modalTitle {
    color: #ffd166;
    font-size: 1.8rem;
    text-align: center;
    margin-bottom: 25px;
    text-transform: uppercase;
    letter-spacing: 1px;
}

/* Candidate List in Modal */
.candidateList {
    max-height: 400px;
    overflow-y: auto;
    margin-bottom: 20px;
    padding-right: 5px;
}

/* Custom Scrollbar for Webkit */
.candidateList::-webkit-scrollbar {
    width: 6px;
}
.candidateList::-webkit-scrollbar-track {
    background: rgba(255,255,255,0.05);
    border-radius: 10px;
}
.candidateList::-webkit-scrollbar-thumb {
    background: rgba(255,255,255,0.2);
    border-radius: 10px;
}

.candidateItem {
    background: rgba(255, 255, 255, 0.1);
    padding: 15px 20px;
    margin-bottom: 12px;
    border-radius: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    border: 2px solid transparent;
    transition: all 0.2s ease;
}

.candidateItem:hover {
    background: rgba(255, 255, 255, 0.15);
    transform: translateX(5px);
}

.candidateItem.selected {
    background: rgba(255, 107, 107, 0.25);
    border-color: #ff6b6b;
}

.cName {
    color: white;
    font-size: 1.1rem;
    font-weight: 500;
}

.cRoll {
    color: rgba(255,255,255,0.6);
    font-size: 0.9rem;
}

.submitVoteBtn {
    width: 100%;
    padding: 15px;
    background: #ff6b6b;
    color: white;
    border: none;
    border-radius: 50px;
    font-size: 1.1rem;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
    transition: background 0.3s;
    opacity: 0.5;
    pointer-events: none;
}

.submitVoteBtn.active {
    opacity: 1;
    pointer-events: all;
}

.submitVoteBtn:hover {
    background: #ff5252;
}

/* Success Message */
.successMsg {
    text-align: center;
    color: #4CAF50;
    background: rgba(255,255,255,0.9);
    padding: 10px;
    border-radius: 10px;
    margin-top: 15px;
    font-weight: bold;
    animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
    from { opacity: 0; transform: translateY(10px); }
    to { opacity: 1; transform: translateY(0); }
}

@media (max-width: 600px) {
    .title { font-size: 2.5rem; letter-spacing: 3px; }
    .subtitle { font-size: 1.2rem; }
    .totalVotesBadge { 
        top: auto; 
        bottom: 20px; 
        right: 50%; 
        transform: translateX(50%); 
        width: max-content; 
    }
    .totalVotesBadge:hover { transform: translateX(50%) scale(1.05); }
}