/* src/CandidateVoteCount.module.css */
.cardContainer {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    padding: 25px;
    text-align: center;
    width: 250px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto; /* Centers in grid */
}

.cardContainer:hover {
    transform: translateY(-5px);
    box-shadow: 0 12px 40px rgba(0, 0, 0, 0.3);
    border-color: rgba(255, 209, 102, 0.3);
}

.name {
    color: #ffd166;
    font-size: 1.4rem;
    font-weight: bold;
    margin-bottom: 5px;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-shadow: 2px 2px 0px rgba(0,0,0,0.2);
}

.rollNumber {
    color: rgba(255, 255, 255, 0.6);
    font-size: 0.9rem;
    margin-bottom: 20px;
    font-style: italic;
}

.voteBox {
    background: rgba(0, 0, 0, 0.25);
    border-radius: 15px;
    padding: 15px 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.count {
    font-size: 2.5rem;
    font-weight: 800;
    color: white;
    line-height: 1;
    margin-bottom: 5px;
    text-shadow: 0 4px 10px rgba(0,0,0,0.3);
}

.label {
    color: #ff6b6b;
    font-size: 0.85rem;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
}